.App {
  text-align: center;
}

.App-logo {
  height: 33vmin;
  pointer-events: none;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 8s alternate;
  }
}

.App-header {
  background-color: #000000;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-header h2,h1{
  
}


.App-link {
  color: #c00000;
}

@keyframes App-logo-spin {
  from {
    transform: translate(-20px);
  }
  to {
    transform: translate(20px);
  }
}
